import React from 'react';

const mode = (props) => {
  // const canPlay = props.status === ''
  return (
    <div className="col-md-3 playlistArt musicMode">
        <div className={`modeImg ${props.path}`} >
        <div className="modeHover">
          {props.icon !== '' ? <i className="fa fa-circle fa-stack-2x icon-background playlistButton button play" onClick={props.play} ></i> : null}

          <i className={`fa fa-${props.icon}${props.icon !== 'refresh' ? '-circle' : ''} fa-stack-1x playlistButton themeColor button play`} aria-hidden="true" onClick={props.play}></i>
        </div>
        <div className="modeName">
          {props.name}
        </div>
        </div>
    </div>
  );
}
export default mode;


