import React, { Component } from 'react';
import Sound from 'react-sound';
export default class playPause extends Component {
  render() {

    const loadingDom = <i className="fa fa-refresh themeColor playerButton button loader" aria-hidden="true"></i>;
    return (
        <div className="musicControl-time">
        {this.props.loaded === 0 && this.props.current === 0 && this.props.status !== Sound.status.STOPPED ? loadingDom : this.props.status === Sound.status.PAUSED || this.props.status === Sound.status.STOPPED ?
            <i className="fa fa-play-circle playerButton themeColor button play" onClick={this.props.handlePlay} aria-hidden="true"></i> :
            <i className="fa fa-pause-circle playerButton themeColor button pause" aria-hidden="true" onClick={this.props.handlePause}></i> }
            <div className="volume-container">{/*
              <div className={`toggleControlButton button left ${!this.props.repeat ? 'released' : 'pressed'}`} onClick={this.props.handleRepeat}>
                <i className="fa fa-repeat" aria-hidden="true"></i>
              </div>
              <div className={`toggleControlButton button right ${this.props.muted ? 'released' : 'pressed'}`} onClick={this.props.handleMute}>
                <i className={`fa ${this.props.muted ? 'fa-volume-off' : 'fa-volume-up' }`} aria-hidden="true"></i></div>
                // mute and repeat button
            */}
                <i className={` fa fa-volume-off`} aria-hidden="true"></i>
                <div className="volumeRange">
                  <input className="button"
                    type="range"
                    max="100"
                    ref="volume"
                    min="0"
                    defaultValue={this.props.volume}
                    onChange={() => this.props.setVolume(+this.refs.volume.value)}/>
                </div>
                <i className={` fa fa-volume-up`} aria-hidden="true"></i>
              </div>
        </div>
    );
  }
};

