import React, { Component } from 'react';
import Mode from './mode';
import SongLists from './songsList';
import lodash from 'lodash';
import Sound from 'react-sound';

export default class Playlist extends Component {
    render() {
        return(
        <div className="musicBody">
            {this.generateList()}
        </div>
        );
    }
    generateList() {
      const list = [];
      // const filteredPlaylist = lodash.filter(SongLists, (item) => {
      //   return item.path !== this.props.path;
      // })
      lodash.forOwn(SongLists, (item, key) => {
        // const icon = this.props.path === item.path && this.props.status !== Sound.status.PAUSED;
        let icon = 'play';
        if (this.props.path === item.path ) {
          switch(this.props.status) {
          case Sound.status.PLAYING:
            icon = 'pause';
            break;
          case Sound.status.PAUSED:
          case Sound.status.STOPPED:
            icon = 'play';
            break;
          default:
            icon = '';
            break;
          }
          if(this.props.current === 0 && Sound.status.STOPPED !== this.props.status) {
            icon = '';
          }
        }
        list.push(<Mode
          name={item.mode}
          path={item.path}
          key={key}
          current={this.props.current}
          pauseIcon={this.props.path === item.path && this.props.status !== Sound.status.PAUSED}
          icon={icon}
          status={this.props.status}
          play={this.props.handlePlaylistPlay.bind(this, item.path)}
        />);
      });
      return list;
    }
}


