import React, { Component } from 'react';
import './App.css';
import Sound from 'react-sound';
import PlayPauseControl from './controls/PlayPause';
import Playlist from './playlist';
import SongLists from './songsList';
import _ from 'lodash';
// import StatusFooter from './errorPlate';

class App extends Component {
  constructor(props) {
    super(props);
    const defaultPath = 'productivity';
    this.state = {
      status: Sound.status.STOPPED,
      position: 8550,
      loaded: 0,
      current: 0,
      currentTime: "00:00",
      endTime: "00:00",
      volume: 80,
      repeat: false,
      muted: false,
      path: defaultPath,
      songId: this.randomSongId(defaultPath),
      pos: 0,
      duration: 0
    };
    this.handleSongLoading = this.handleSongLoading.bind(this);
    this.handleSongPlaying = this.handleSongPlaying.bind(this);
    this.handleSongFinishedPlaying = this.handleSongFinishedPlaying.bind(this);
    this.handlePlay = this.handlePlay.bind(this);
    this.handlePause = this.handlePause.bind(this);
    this.handleVolumeChange = this.handleVolumeChange.bind(this);
    this.handleRepeat = this.handleRepeat.bind(this);
    this.handleMute = this.handleMute.bind(this);
    this.handlePlaylistPlay = this.handlePlaylistPlay.bind(this);
    this.handleSeek = this.handleSeek.bind(this);
    this.onload = this.onload.bind(this);
  }
  // componentDidMount() {
  //    window.addEventListener('scroll', this.handleScroll);
  // }

  render() {
    const currentPlaylist = _.find(SongLists, (item) => {
      return item.path === this.state.path;
    });

    return (
      <div className="App">
        <div className="App-header">
          <div>
            <div className="logo">
          </div>
          <div style={{display: 'block'}}></div>
            <div className="vol-art-container">
              <div className={`albumArt col-md-3 ${this.state.path}`}>
              </div>
            </div>


          <div className="player-button-container">
            <div className="headerName">{currentPlaylist.mode}</div>
            <div className="songName">{currentPlaylist.songs[this.state.songId].title}</div>
            <PlayPauseControl
              status={this.state.status}
              handlePlay={this.handlePlay}
              handlePause={this.handlePause}
              currentTime={this.state.currentTime}
              endTime={this.state.endTime}
              volume={this.state.volume}
              setVolume={this.handleVolumeChange}
              repeat={this.state.repeat}
              muted={this.state.muted}
              handleRepeat={this.handleRepeat}
              handleMute={this.handleMute}
              loaded={this.state.loaded}
              current={this.state.current}
            />
          </div>
            {/*Render time and range*/}
          </div>
        </div>
        <Playlist
        handlePlaylistPlay={this.handlePlaylistPlay}
        path={this.state.path}
        current={this.state.current}
        status={this.state.status}/>
        <div>Rhythm Mind | An elegant music player to help you concentrate and increase productivity. <br/>Beta Version</div>
        <Sound
            url={currentPlaylist.songs[this.state.songId].url}
            playStatus={this.state.status}
            playFromPosition={this.state.position /* in milliseconds */}
            onLoading={this.handleSongLoading}
            onPlaying={this.handleSongPlaying}
            onFinishedPlaying={this.handleSongFinishedPlaying}
            volume={this.state.volume}
            repeat={this.state.repeat}
            onload={this.onload}
            catchError={this.catchError}
            position={this.state.pos}/>
            {/*<input type="range" min={0} max={this.state.duration} value={this.state.pos} ref="seek" style={{width: '90%'}} onChange={this.handleSeek}/>*/}
            {/*<StatusFooter/>*/}
      </div>

    );
  }
  timeAndRange() {
    return (
      <div className="timeAndRange">
        <div className="endTime time">{this.state.currentTime} / {this.state.endTime}</div>
        <div style={{clear: 'both'}}></div>
        <div className="rangeContainer range">
          <div className="rangePlayed range" style={{width: `${this.state.current}%`}}></div>
          <div className="rangeLoaded range" style={{width: `${this.state.loaded}%`}}></div>
                {/*<div className="startTime time">{this.state.currentTime}</div>
                <div className="endTime time">{this.state.endTime}</div>*/}
        </div>
        <div style={{clear: 'both'}}></div>
      </div>
    );
  }
  onload(o) {
    // console.log('ERRRRRRRR*******', o);
  }
  handleSeek() {
    this.setState({
      pos: +this.refs.seek.value
    });
  }
  handleVolumeChange(val) {
    this.setState({
      volume: val
    })
  }
  handleRepeat() {
    this.setState({
      repeat: !this.state.repeat
    })
  }
  convert(milliseconds) {
    const seconds = parseInt(milliseconds / 1000, 10) % 60 ;
    const minutes = parseInt(milliseconds / (1000 * 60), 10) % 60;
    const hours = parseInt(milliseconds / (1000 * 60 * 60), 10) % 24;
    const showHours = this.padZero(hours) + ':';
    return `${hours > 0 ? showHours : ''}${this.padZero(minutes)}:${this.padZero(seconds)}`;
  }
  padZero(number) {
    return number < 10 ? '0' + number : number;
  }
  handlePlaylistPlay(newPlaylist) {
    let updatedState = {
      path: newPlaylist,
      current: 0,
      loaded: 0,
      pos: 0,
      songId: this.randomSongId(newPlaylist),
      status: Sound.status.PLAYING
    };
    if (newPlaylist === this.state.path) {
      updatedState = {
        status: this.state.status === Sound.status.PAUSED || this.state.status === Sound.status.STOPPED ? Sound.status.PLAYING : Sound.status.PAUSED
      };
    }
    this.setState(updatedState);
  }
  handleSongPlaying(song) {
    const current = (song.position / song.duration) * 100;
    this.setState({
      current: current,
      currentTime: this.convert(song.position),
      pos: song.position
    });
  }
  handleSongLoading(song) {
    // if (this.state.muted) {
    //   song.mute();
    // } else {
    //   song.unmute();
    // }
    this.setState({
      loaded: song.bytesLoaded * 100,
      endTime: this.convert(song.duration),
      duration: song.duration
    });
  }
  handlePause() {
    this.setState({
      status: Sound.status.PAUSED
    })

  }
  handleMute() {
    this.setState({
      muted: !this.state.muted
    })
  }
  handlePlay() {
    this.setState({
      status: Sound.status.PLAYING
    });
  }
  handleStop() {
    this.setState({
      status: Sound.status.STOPPED
    });
  }
  handleSongFinishedPlaying() {

    this.setState({
      songId: this.nextRandomSongId(),
      loaded: 0,
      current: 0,
      pos: 0
    });
  }
  nextRandomSongId() {
    const currentPlaylist = _.find(SongLists, (item) => {
      return item.path === this.state.path;
      });
    const filteredPlaylist = _.filter(currentPlaylist.songs, (item) => {
      return +item.id !== +this.state.songId;
    })
    const sizeOfFiltered = _.size(filteredPlaylist);
    let newSongId = this.state.songId;
    if (sizeOfFiltered > 0) {
      newSongId = filteredPlaylist[_.random(sizeOfFiltered - 1)].id
    }
    return newSongId;
  }
  randomSongId(newPlaylist) {
    const currentPlaylist = _.find(SongLists, (item) => {
      return item.path === newPlaylist;
    });
    const sizeOfFiltered = _.size(currentPlaylist.songs);
    const keys = _.keys(currentPlaylist.songs);
    return keys[_.random(sizeOfFiltered - 1)];
  }
}

export default App;



